"use client"

import Link from "next/link"
import { useState, useEffect } from "react"

interface SpiralIcon {
  id: number
  x: number
  y: number
}

// Generate spiral grid positions
function generateSpiralGrid(count: number, spacing: number = 80): SpiralIcon[] {
  const icons: SpiralIcon[] = []
  let x = 0
  let y = 0
  let dx = 0
  let dy = -1
  let segmentLength = 1
  let segmentPassed = 0

  for (let i = 0; i < count; i++) {
    icons.push({ id: i, x, y })
    
    // Move to next position
    x += dx * spacing
    y += dy * spacing
    segmentPassed++
    
    // Change direction when segment is complete
    if (segmentPassed === segmentLength) {
      segmentPassed = 0
      
      // Turn right
      const temp = dx
      dx = -dy
      dy = temp
      
      // Increase segment length every two turns
      if (dy === 0) {
        segmentLength++
      }
    }
  }
  
  return icons
}

export default function FilmPage() {
  const [icons, setIcons] = useState<SpiralIcon[]>([])
  const [center, setCenter] = useState({ x: 0, y: 0 })

  useEffect(() => {
    // Calculate center of viewport
    const centerX = window.innerWidth / 2
    const centerY = window.innerHeight / 2
    setCenter({ x: centerX, y: centerY })
    
    // Generate spiral grid with 20 icons (you can adjust this number)
    const spiralIcons = generateSpiralGrid(20, 100)
    setIcons(spiralIcons)
  }, [])

  return (
    <div style={{ 
      position: "relative",
      backgroundColor: "#009E1A", 
      minHeight: "100vh", 
      width: "100%",
      overflow: "hidden"
    }}>
      <Link href="/" style={{ position: "absolute", top: "20px", left: "20px", zIndex: 10 }}>
        <img src="/assets/scat_logo.svg" alt="SCAT Logo" style={{ width: "30px", height: "30px", cursor: "pointer" }} />
      </Link>
      
      {/* Spiral Grid Container */}
      <div style={{
        position: "absolute",
        left: `${center.x}px`,
        top: `${center.y}px`,
        transform: "translate(-50%, -50%)",
        width: "100%",
        height: "100%"
      }}>
        {icons.map((icon) => (
          <div
            key={icon.id}
            style={{
              position: "absolute",
              left: `${icon.x}px`,
              top: `${icon.y}px`,
              transform: "translate(-50%, -50%)",
              width: "60px",
              height: "60px",
              backgroundColor: "rgba(255, 255, 255, 0.2)",
              borderRadius: "50%",
              border: "2px solid rgba(255, 255, 255, 0.4)",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              cursor: "pointer",
              transition: "all 0.3s ease",
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.backgroundColor = "rgba(255, 255, 255, 0.4)"
              e.currentTarget.style.transform = "translate(-50%, -50%) scale(1.2)"
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.backgroundColor = "rgba(255, 255, 255, 0.2)"
              e.currentTarget.style.transform = "translate(-50%, -50%) scale(1)"
            }}
          >
            {/* Placeholder for icon - replace with actual icon/image */}
            <span style={{ color: "white", fontSize: "24px" }}>â€¢</span>
          </div>
        ))}
      </div>
    </div>
  )
}
